@import url(https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

h1, h2 {
    margin: 0;
    padding: 5px;
    font-weight: bold;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.5em;
}

h3 {
    margin: 1em;
    font-size: 1.3em;
    font-weight: bold;
}

p {
    font-size: 1.3em;
}

body {
    font-family: Garamond;
    margin: 0;
    display: flex;
    flex-direction: column;
    background: #fbfbec;
}
  
.header {
    padding: 15px;
    margin-bottom: 3px;
    background: #fdbb00;
    color: #fff;
    display: inline;
    border: thick double #fff;
    display: flex;
}

.footer {
    padding: 15px;
    margin-top: 3px;
    background: #5b8ef0;
    color: #fff;
    display: inline;
    border: thick double #fff;
    display: flex;
    align-content: center;
    text-align: center;
}

small {
    display: block;
    font-size: 100%;
    width: 100%;
}

@media screen and (max-width: 700px) {
    header {
        display: inline-block;
    }
}

.nav {
    display: flex;
    justify-content: flex-end;
    background-color: #5b8ef0;
    border: thick double #fff;
    line-height: 3em;
}

.nav a {
    font-weight: bold;
    font-size: 1.1em;
    color: #fff;
    padding-right: 1em;
    text-decoration: none;
    text-align: center;
    line-height: auto;
}

.nav a:hover {
    text-decoration: underline;
    text-underline-offset: .3em;
    text-decoration-thickness: 2px;
}

.main {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.flex {
    display: flex;
}

.card {
    border-radius: 10px;
    background-image: url("https://via.placeholder.com/300x200");
    background-repeat: no-repeat;
    background-size: contain;
    padding: 5px;
    margin: 1em 0 1em 1em;
    float: left;
    height: 200px;
    width: 300px;
    border: thick double black;
}

.card h2 {
    background: rgba(255, 255, 255, .9);
    border-radius: 10px;
    text-align: center;
    margin-bottom: 45%;
}

.card a {
    background: rgba(0, 81, 255, 0.5);
    border-radius: 10px;
    padding: .5em;
    font-weight: bold;
    margin-right: .5em;
    margin-bottom: .5em;
    color: #fff;
}

.card a:hover {
    background-color: rgb(0, 81, 255);
}

.card a:active {
    background-color: white;
    color: blue;
}

.contactCard {
    background-color: rgb(252, 213, 161);
    border: thick solid white;
    margin: .5rem 0 1rem 0;
}

.contactCard a {
    color: black;
}

.contactCard a h3:hover {
    text-decoration: underline;
    text-underline-offset: .2em;
    text-decoration-thickness: 1px;
}

.link {
    padding: 0 0.3em;
    vertical-align: bottom;
    align-self: flex-end;
}

.link a img:hover {
    background-color: rgba(255, 245, 245, 0.75);
    box-shadow: 0px 0px 40px snow;
    border-radius: .25em;
}

#about {
    display: flex;
    flex-direction: row;
}

#photo {
    margin-right: 1em;
    float: left;
    height: 200px;
    width: 200px;
}

#pageTitle {
    flex: 100% 1;
    text-align: center;
    padding-left: 120px;
}

#linkedIn, #gitHub {
    display: inline-block;
}

#icons {
    display: flex;
}

@media screen and (max-width: 700px) {
    #pageTitle {
        width: 100%;
        padding-left: 0;
        margin-bottom: 1em;
    }
    #icons {
        float: right; 
    }
    .container {
        width: 100% !important;
    }
    h3 {
        font-size: 1.1em;
    }
}

#profile {
    border-radius: 10px;
    margin-left: 1em;
    background-color: #fff;
}

#profile p {
    max-width: 900px;
    padding: 1em;
    line-height: 1.5em;
}

#download {
    margin-left: 1em;
}

#resume a:hover {
    background-color: blue !important;
}

#copyright {
    margin: auto;
}
